*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  display: grid;
  background: url('../src/images/bg_img.jpeg') no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  align-content: center;
}
#drum-machine{
  display: grid;
 justify-content: center;
  grid-template-columns: repeat(auto-fit,140px);
}
.drum-pad{
  display: flex;
  justify-content: center;
  border: .4rem solid black;
  border-radius: .5rem;
  margin: 1rem;
  font-size: 1.5rem;
  padding: .5rem ;
  transition: all .07s ease;
  text-align: center;
  color: #ffc600;
  background: rgba(0,0,0,0.4);
  text-shadow: 0 0 0.5rem black;
  cursor: pointer;
}

.drum-pad kbd{
  font-size: 2.5em;
}
#display{
  position: fixed;
  top: 0;
  display: block;
  width: 100%;
  background: rgba(0,0,0,0.4);
  color: #ffc600;
  font-size: 3em;
  text-align: center;
}
.animator {
  transform: scale(1.1);
  border-color: #ffc600;
  box-shadow: 0 0 1rem #ffc600;
}

@media (max-width:30em){
  #drum-machine{
    grid-template-columns: repeat(auto-fit,120px);
  }
}

